/**
  layout
*/

.gap-md {
  @apply gap-2;
}

.gap-lg {
  @apply gap-4;
}


.p-md {
  @apply p-2;
}

.p-lg {
  @apply p-4;
}

.stack-v {
  @apply flex flex-col;
}

.stack-h {
  @apply flex flex-row;
}
